/* Menu
 ------------------------------------------------------------*/
.main-menu {
  padding: 10px 0;
  margin: 0 10px 0 0;
  list-style: none;
  display: flex;
  flex: 0 0 820px;
  max-width: 820px;
  align-self: flex-start;
  justify-content: space-between; }

.main-menu__item {
  position: relative; }
  .main-menu__item:hover .main-menu__link::before {
    width: 100%; }

.main-menu__link {
  position: relative;
  padding: 14px 0;
  display: block;
  font-size: 1.3rem;
  color: #1b253b;
  font-family: 'Ubuntu', sans-serif;
  font-weight: bold;
  text-transform: uppercase;
  transition: color 0.3s ease; }
  .main-menu__link::before {
    content: attr(data-text);
    position: absolute;
    top: 50%;
    left: 0;
    width: 0;
    overflow: hidden;
    color: #40b5f8;
    transition: width 0.4s ease;
    white-space: nowrap;
    transform: translateY(-50.5%); }
  .main-menu__link::after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 8px;
    width: 31px;
    height: 3px;
    opacity: 0;
    transform: translateX(-10px);
    background-color: #40b5f8;
    transition: all 0.3s ease; }
  .main-menu__link:hover::after {
    opacity: 1;
    transform: translateX(0); }

.main-menu__link_active {
  color: #40b5f8; }
  .main-menu__link_active::after {
    opacity: 1;
    transform: translateX(0); }
  .main-menu__link_active:active, .main-menu__link_active:visited {
    color: #40b5f8; }

.main-menu__item.item__has-dropdown:hover > .dropdown-menu,
.main-menu__item.item__has-dropdown:hover > .dropdown-submenu {
  opacity: 1;
  visibility: visible; }

.main-menu__dropdown {
  position: absolute;
  top: 45px;
  left: 0;
  padding: 0;
  margin: 0;
  list-style: none;
  border: 1px solid #dcdcdc;
  border-radius: 4px;
  opacity: 0;
  visibility: hidden;
  z-index: 1;
  transition: all 0.3s ease;
  background: #fff; }
  .main-menu__dropdown.dropdown-menu {
    transform: translateY(30px); }
    .main-menu__dropdown.dropdown-menu > .main-menu__item::after {
      content: '';
      width: 6px;
      height: 11px;
      display: block;
      position: absolute;
      right: 15px;
      top: 50%;
      transform: translateY(-50%);
      background-image: url("../images/icons/arrow.svg");
      background-position: center center;
      background-repeat: no-repeat; }
    .main-menu__dropdown.dropdown-menu > .main-menu__item:hover::after {
      background-image: url("../images/icons/arrow_hover.svg"); }
  .main-menu__dropdown.dropdown-submenu {
    top: -1px;
    left: -2px;
    transform: translateX(108%); }
  .main-menu__dropdown .main-menu__item {
    width: 334px;
    border-bottom: 1px solid #dcdcdc; }
    .main-menu__dropdown .main-menu__item:last-child {
      border: none; }
    .main-menu__dropdown .main-menu__item:hover {
      background-color: #e4f5ff; }
      .main-menu__dropdown .main-menu__item:hover > .main-menu__link {
        color: #40b5f8;
        transform: translateX(12px); }
  .main-menu__dropdown .main-menu__link {
    font-size: 1.4rem;
    padding: 11px 20px 13px;
    font-weight: 400;
    text-transform: none;
    transition: transform .3s ease, color .3s ease; }
    .main-menu__dropdown .main-menu__link::after, .main-menu__dropdown .main-menu__link::before {
      content: none; }

.main-menu__item.item__has-dropdown:hover > .dropdown-menu {
  transform: translateY(0); }

.main-menu__item.item__has-dropdown:hover > .dropdown-submenu {
  transform: translateX(100%); }

